.container{
  height: 100vh;
  background-color: black;
}
.center{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.hover{
  position: relative;
  width: 400px;
  height: 400px;
}
.box{
  background-color: #363dc2;
  width: 200px;
  height: 200px;
  position: absolute;
  top: 20%;
  left : 10%;
  -webkit-transition: all 0.1s linear;
  -o-transition: all 0.1s linear;
  transition: all 0.1s linear;
}
.text{
  color: white;
  font-size: 50px;
  position: absolute;
  top: 40%;
  left : 70%;
  -webkit-transition: all 0.1s linear;
  -o-transition: all 0.1s linear;
  transition: all 0.1s linear;
}



